<template>
    <div class="content">
        <div class="booktop">
            <div class="mainperson">
                <span></span>
                <input type="text" id="cardsNum2" v-model="addbook.mainperson"  @keyup="getGroup">
                <ul id="getGroup" v-show="isshowbookorders">
                    <li v-for="(v,i) in mainpersons" :key="i" @click="setvalue(v.NAME,v.ID)">{{v.NAME}}({{v.ID}})</li>
                </ul>
            </div>
            <div class="bookstype">
                <div class="title"></div>
                <div>
                    <input type="radio" v-model="addbook.bookstype" @change="isshowOA1(1)" value="1" id="churchyard" name="radios">
                    <label for="churchyard"></label>
                    <input type="radio" v-model="addbook.bookstype" @change="isshowOA1(0)" value="0" id="overseas" name="radios">
                    <label for="overseas"></label>
                </div>
            </div>
            <div class="bookstype">
                <div class="title"></div>
                <div>
                    <input type="radio" v-model="addbook.Fundstype" id="scientific"  value=""  name="expenditure">
                    <label for="scientific"></label>
                    <input type="radio" v-model="addbook.Fundstype" id="others"  value=""  name="expenditure">
                    <label for="others"></label>
                </div>
            </div>
            <mt-field label="" placeholder="" v-model="addbook.Fundsnum"></mt-field>
            <mt-field label="" placeholder="" v-model="addbook.invoicenumber" @change.native="checkInvoiceNum(addbook.invoicenumber)"></mt-field>
            <mt-field label="" placeholder="" v-model="addbook.invoicenvalue" @change.native="isshowOA(addbook.invoicenvalue)"></mt-field>
            <mt-field label="" placeholder="" v-model="addbook.instructions"></mt-field>
        </div>
        <div class="bookbottom">
            <mt-cell title="" is-link @click.native="upload">
                <img v-for="(v,i) in bookorders.slice(0,3)" :key="i" :src="domain+'/fileManage/get?ID='+v.ID" height="30px" width="30px">
                <span style="color: #ccc" v-show="bookorders.length>3">...</span>
            </mt-cell>
            <mt-cell title="" >
                <img v-show="FPfiles[0].ID" :preview='1' :src="domain+'/fileManage/get?ID='+FPfiles[0].ID" height="30px" width="30px">
                <span class="rightadd" @click="uploadFP">
                    <upload style="display:none;"  ref="upload" exts="png|jpg|bmp|gif|PNG|JPG|HEIC|JPEG|jpeg" v-on:choose="choosefiel" v-on:done="finish" :url="uploadUrl"></upload>
                </span>
            </mt-cell>
            <mt-cell title="OA" v-if="showOA == 'true'">
                <img v-show="OAfiles[0].ID" :preview='2' :src="domain+'/fileManage/get?ID='+OAfiles[0].ID" height="30px" width="30px">
                <span class="rightadd"  @click="uploadOA">
                    <upload style="display:none;"  ref="uploadOA" exts="png|jpg|bmp|gif|PNG|JPG|HEIC|JPEG|jpeg" v-on:choose="choosefiel" v-on:done="finishOA" :url="uploadUrl"></upload>
                </span>
            </mt-cell>
             <mt-cell title="" v-if="showOA == 'true'" >
                <img v-show="DOCfiles[0].ID" :preview='3' :src="domain+'/fileManage/get?ID='+DOCfiles[0].ID" height="30px" width="30px">
                <span class="rightadd" @click="uploadDOC">
                    <upload style="display:none;"  ref="uploadDOC" :multiple='true' exts="png|jpg|bmp|gif|pdf|PNG|JPG|HEIC|JPEG|jpeg" v-on:choose="choosefiel" v-on:done="finishDOC" :url="uploadUrl"></upload>
                </span>
            </mt-cell>

        </div>
        <div class="booklist" v-if="isadd">
            <div class="booklistsingle">
                <ul class="bookmiddle">
                    <li v-for="(item,index) in addArr" :key="index">
                        <i class="right_del" @click="del(index)"></i>
                        <div class="middle_left" @click="edit(item)" :style="{background:'url('+item.IMGURL+')',backgroundSize:'cover'}"></div>
                        <div class="middle_right">
                            <h6  @click="edit(item)">{{item.BOOKNAME}}</h6>
                            <p  @click="edit(item)">: {{item.AUTHOR}}</p>
                            <p  @click="edit(item)">ISBN:{{item.ISBN}}</p>
                            <p class="last">{{item.TRADINGPRICE}}
                                <span>
                                    <i class="reduce" @click="reduce(item,index)"></i>
                                    <span class="text">{{item.QUANTITY}}</span>
                                    <i class="add" @click="add(item)"></i>
                                </span>
                            </p>
                        </div>
                    </li>
                </ul>
                <div class="totalnum">:<span>{{sum}}</span></div>
            </div>
        </div>
        <div class="bookfixedbottom">
            <div class="bookfixedbottomleft" @click="adddetail"></div>
            <div class="bookfixedbottommiddle" @click="submit"></div>
            <div class="bookfixedbottomright" @click="back"></div>
        </div>
        <div class="meng" v-show="ismeng">
            <p></p>
        </div>
    </div>
</template>
<script>
import Vue from 'vue'
import code from '../util/code'
import upload from '../components/upload'
import { global } from '../util/global'
export default {
    components:{
        upload
    },
    data() {
        return {
            isadd:'',//
            bookorders:[],//--
            isshowbookorders:false,//
            mainpersons:[],//
            addbook:{
                mainperson:'',//
                bookstype:'',//
                Fundstype:'',//
                Fundsnum:'',//
                invoicenumber:'',//
                invoicenvalue:'',//
                instructions:'',//
            },
            addArr:[],//
            addArrs:[],//
            sum:0,//
            uploadUrl:'',//url
            FPfiles:[{ID:''}],//
            showOA:'',//OA
            OAfiles:[{ID:''}],//OA
            DOCfiles:[{ID:''}],//DOC
            domain:global,
            ismeng:false,//
        }
    },
    methods: {
        back(){
            this.$router.push('/');
            this.$store.commit('setselected','')
            // window.sessionStorage.setItem('selected','');
        },
        upload(){
            var obj = {
                bookstype:this.addbook.bookstype,//
                invoicenumber:this.addbook.invoicenumber,//
                invoicenvalue:this.addbook.invoicenvalue,//
                mainperson:this.addbook.mainperson,
                Fundstype:this.addbook.Fundstype,
                Fundsnum:this.addbook.Fundsnum,
                instructions:this.addbook.instructions,
            }
            window.sessionStorage.setItem('addbook',JSON.stringify(obj))
            //
            window.sessionStorage.setItem('formbook','formbook');
            this.$router.push('addupload')
        },
         //url
        uploadFile(){
          this.util.getUrlByCode(code.base,code.fileManageupload).then(res => {
            this.uploadUrl = res;
          })
        },
        choosefiel(file){
            console.log("");
        },
        finish(res){
            console.log("");
            this.FPfiles = []; 
            res.FILE.forEach(element => {
                this.FPfiles.push(element);
            });
            Vue.set(this.FPfiles,res);
            window.sessionStorage.setItem('FPfiles',JSON.stringify(this.FPfiles))
        },
        finishOA(res){
            this.OAfiles = [];
            res.FILE.forEach(element => {
                this.OAfiles.push(element);
            });
            Vue.set(this.OAfiles,res);
            window.sessionStorage.setItem('OAfiles',JSON.stringify(this.OAfiles))
        },
        finishDOC(res){
            this.DOCfiles = [];
            res.FILE.forEach(element => {
                this.DOCfiles.push(element);
            });
            Vue.set(this.DOCfiles,res);
            window.sessionStorage.setItem('DOCfiles',JSON.stringify(this.DOCfiles))
        },
        //
        uploadFP(){
            this.$refs.upload.toupload();
        },
        //OA
        uploadOA(){
            this.$refs.uploadOA.toupload();
        },
        //pdf
        uploadDOC(){
            this.$refs.uploadDOC.toupload();
        },
        //
        adddetail(){
            window.sessionStorage.removeItem('singlelist');
            window.sessionStorage.setItem('isedit','false');//iseditfalse
            if(!this.addbook.mainperson){
                this.Toast('')
            }else if(!this.addbook.bookstype){
                this.Toast('')
            }else if(!this.addbook.Fundstype){
                this.Toast('')
            }else if(!this.addbook.Fundsnum){
                this.Toast('')
            }else if(!this.addbook.invoicenumber){
                this.Toast('')
            }else if(!this.addbook.invoicenvalue){
                this.Toast('')
            }else if(!this.addbook.instructions){
                this.Toast('')
            }else if(this.bookorders.length<1){
                this.Toast('')
            }else if(!this.FPfiles[0].ID){
                this.Toast('');
            }else{
                // this.$store.commit('setFundstype',this.bookstype);//
                this.$router.push('adddetail')
            }
        },
        //
        getGroup(e){
            // if(this.addbook.mainperson==''){
            //     return false;
            // }
            this.isshowbookorders = true;
            this.util.postAjax({
                code:code.base,
                url:code.bookgetGroup,
                data:{
                    page:1,
                    limit:10,
                    filter: JSON.stringify({
                        'KEYWORD':this.addbook.mainperson,
                    })
                }
            }).then( res => {
                console.log(res)
                if(res.items.length == 0){
                    this.mainpersons = [];
                    this.addbook.mainperson = ''
                }else{
                    this.mainpersons = res.items;
                }
                
            })
        },
        //
        setvalue(data,id){
            this.addbook.mainperson = data;
            //id
            window.sessionStorage.setItem('mainpersonID',id)
            this.mainpersons = [];
            this.isshowbookorders = false;
        },
        //
        checkInvoiceNum(val){
            var ID = '';
            if(window.sessionStorage.getItem('isadd') == 'true'){
                ID = '';
            }else{
                ID = window.sessionStorage.getItem('editID');
            }
            if(val){
                this.util.postAjax({
                    code:code.base,
                    url:code.checkInvoiceNum,
                    data:{
                        INVOICENUM:val,
                        ID:ID
                    }
                }).then( res => {
                    if(!res.status){
                        this.Toast("");
                        this.addbook.invoicenumber= ''
                    }
                })
            }else{
                this.Toast('')
            }
        },
        //OA
        isshowOA(num){
            if(this.addbook.bookstype == 1){//
                if(num > 2000){
                    this.showOA = 'true';
                    window.sessionStorage.setItem('showOA','true');
                }else{
                    this.showOA = 'false';
                    window.sessionStorage.setItem('showOA','false');

                }
            }else{
                if(num > 5000){
                    this.showOA = 'true';
                    window.sessionStorage.setItem('showOA','true');
                }else{
                    this.showOA = 'false';
                    window.sessionStorage.setItem('showOA','false');
                }
            }
        },
        isshowOA1(num){
            if(num == 1){//
                if(this.addbook.invoicenvalue > 2000){
                    this.showOA = 'true';
                    window.sessionStorage.setItem('showOA','true');
                }else{
                    this.showOA = 'false';
                    window.sessionStorage.setItem('showOA','false');
                }
            }else{
                if(this.addbook.invoicenvalue > 5000){
                    this.showOA = 'true';
                    window.sessionStorage.setItem('showOA','true');
                }else{
                    this.showOA = 'false';
                    window.sessionStorage.setItem('showOA','false');
                }
            }
        },
        //
        edit(data){
            window.sessionStorage.setItem('isedit','true');//iseditfalse
            var addlist = JSON.parse(window.sessionStorage.getItem('addlist'));
            addlist.forEach((e,i)=>{
                if(data.ID == e.ID){
                    addlist.splice(i, 1);
                }
            })
            window.sessionStorage.setItem('singlelist',JSON.stringify(data))
            this.$router.push('adddetail')
        },
        //
        submit(){
            if(!this.addbook.mainperson){
                this.Toast('');
            }else if(!this.addbook.bookstype){
                this.Toast('');
            }else if(!this.addbook.Fundstype){
                this.Toast('');
            }else if(!this.addbook.Fundsnum){
                this.Toast('');
            }else if(!this.addbook.invoicenumber){
                this.Toast('');
            }else if(!this.addbook.invoicenvalue){
                this.Toast('');
            }else if(!this.addbook.instructions){
                this.Toast('');
            }else if(this.bookorders.length<1){
                this.Toast('');
            }else if(!this.FPfiles[0].ID){
                this.Toast('');
            }else if(window.sessionStorage.getItem('showOA')== 'true' && !this.OAfiles[0].ID){
                 this.Toast('OA');
            }else if(window.sessionStorage.getItem('showOA')== 'true' && !this.DOCfiles[0].ID){
                    this.Toast('');
            }else if(this.addArr.length < 1){
                this.Toast('');
            }else{
                this.$store.commit('setFundstype',this.bookstype);//
                var obj = {
                    bookstype:this.addbook.bookstype,//
                    invoicenumber:this.addbook.invoicenumber,//
                    invoicenvalue:this.addbook.invoicenvalue,//
                    mainperson:this.addbook.mainperson,
                    Fundstype:this.addbook.Fundstype,
                    Fundsnum:this.addbook.Fundsnum,
                    instructions:this.addbook.instructions,
                    id:window.sessionStorage.getItem('mainpersonID')
                }
                window.sessionStorage.setItem('addbook',JSON.stringify(obj));
                if(this.addbook.invoicenvalue != this.sum){
                    this.Toast('')
                }else{
                     var RECORD = {//--
                        ID: "",  //()
                        "FUNDTYPE": obj.Fundstype,  //(  /)
                        "NOTEALLMONEY":  obj.invoicenvalue, //
                        "INVOICENUM":  obj.invoicenumber, //
                        "FUNDNUMBER":  obj.Fundsnum, //
                        "ALLSUM":  0, //
                        "ALLMONEY":  obj.invoicenvalue,//
                        "ISCHINESE":  obj.bookstype, //1  0
                        "CREATERNAME":  obj.mainperson, //1.3 
                        "CREATERID":  obj.id, //ID1.3
                        "REASON":  obj.instructions, //
                        "BUYTIME":  this.util.formatMYD() //
                    }
                    if(this.isadd == 'false'){
                        RECORD.ID = window.sessionStorage.getItem('editID');
                    }else{
                        RECORD.ID = '';
                    }
                    //
                    var bookslist = JSON.parse(window.sessionStorage.getItem('addlist'));
                    var BOOKS = [];
                    var bookobj = {};
                    var imgobj = {};
                    var ALLSUM = 0;
                    for (let i = 0; i < bookslist.length; i++) {
                        ALLSUM += Number(bookslist[i].QUANTITY);
                        bookobj = {
                            ISCHINESE : obj.bookstype,
                            ISBN : bookslist[i].ISBN,
                            BOOKNAME : bookslist[i].BOOKNAME,
                            AUTHOR : bookslist[i].AUTHOR,
                            PUBLISHER : bookslist[i].PUBLISHER,
                            BIDPRICE : bookslist[i].BIDPRICE,
                            QUANTITY :this.addArr[i].QUANTITY,
                            TRADINGPRICE : bookslist[i].TRADINGPRICE,
                            TOTAL : obj.invoicenvalue,
                            MONEYTYPE : "",
                        }
                        imgobj = {
                            "TYPE":1,
                            "SHOWNAME":"COVER",
                            "URL":bookslist[i].IMGURL
                        }
                        BOOKS.push({
                            BOOK:bookobj,
                            IMG:{
                                COVER:imgobj
                            }
                        })
                    }
                    RECORD.ALLSUM = ALLSUM;
                    //
                    var NOTE = [];
                    this.bookorders.forEach(e=>{
                        NOTE.push({
                            TYPE:0,
                            NOTEID:e.ID
                        })
                    })
                    //
                    NOTE.push({
                         TYPE:2,
                         NOTEID:this.FPfiles[0].ID
                    })
                    if(window.sessionStorage.getItem('showOA')== 'true'){
                        //OA
                        NOTE.push({
                            TYPE:3,
                            NOTEID:this.OAfiles[0].ID
                        })
                        //
                        NOTE.push({
                            TYPE:4,
                            NOTEID:this.DOCfiles[0].ID
                        })
                    }
                    this.ismeng = true;
                    // return false
                    //
                    this.util.postAjax({
                        code:code.base,
                        url:code.booksave,
                        data:{
                            data: JSON.stringify({
                                'RECORD':RECORD,
                                'NOTE':NOTE,
                                'BOOKS':BOOKS,
                            })
                        }
                    }).then( res => {
                        if(res.success){
                            this.ismeng = false;
                            this.Toast(res.message);
                            this.$router.push('/');
                            this.$store.commit('setselected','')
                            //
                            // window.sessionStorage.removeItem('mainpersonID')
                            window.sessionStorage.removeItem('addlist')
                            window.sessionStorage.removeItem('FPfiles')
                            window.sessionStorage.removeItem('addbook')
                        }
                    })

                }
            }

        },
        //
        reduce(i,index){
            if(i.QUANTITY <= 1 ){
                i.QUANTITY = 1;
                // console.log(i.QUANTITY)
                this.Toast('')
                return;
            }else{
                this.sum = 0;//
                i.QUANTITY--;
            }
            // Vue.set(this.addArr,i.QUANTITY);
            this.calctotal()

        },
        add(i){
            this.sum = 0;
            i.QUANTITY++;
            this.calctotal()

        },
        //
        calctotal(){
            this.addArr.forEach(element => {
                // console.log(element.TRADINGPRICE)
                // console.log(element.QUANTITY)
                element.total = Number(element.TRADINGPRICE) * Number(element.QUANTITY);
                this.sum += element.total; 
            });
            // console.log(this.sum)
        },
        //
        del(i){
            //  iindex1
            this.addArr.splice(i, 1);
            //
            window.sessionStorage.addlist = JSON.stringify(this.addArr);
        }

    },
    mounted() {
        this.uploadFile();//
        //isadd
        this.isadd = window.sessionStorage.getItem('isadd');
        this.bookorders = this.$store.state.selectimgs;
        if(window.sessionStorage.getItem('addlist')){
            this.addArr = JSON.parse(window.sessionStorage.getItem('addlist'));
        }
        //
        this.calctotal();
        if(window.sessionStorage.getItem('addbook')){
            this.addbook = JSON.parse(window.sessionStorage.getItem('addbook'));
        }
        //
        if(window.sessionStorage.getItem('FPfiles')){
            this.FPfiles = JSON.parse(window.sessionStorage.getItem('FPfiles'))
        }
        if(window.sessionStorage.getItem('OAfiles')){
            this.OAfiles = JSON.parse(window.sessionStorage.getItem('OAfiles'))
            this.DOCfiles = JSON.parse(window.sessionStorage.getItem('DOCfiles'))
        }  
        if(this.addbook.bookstype == 1){
            if(this.addbook.invoicenvalue>2000){
                this.showOA = 'true';
            }else{
                this.showOA = 'false';
            }
        }else{
            if(this.addbook.invoicenvalue>5000){
                this.showOA = 'true';
            }else{
                this.showOA = 'false';
            }
        }
    },
    watch: {

    }
}
</script>
<style scoped>
.content{
    background:#F5F5F5; 
    padding-bottom: 2.25rem;
}

.booktop{
    width: 100%;
}
.bookbottom{
    margin-top: 1rem;
}
.rightadd{
    display: inline-block;
    width: 1rem;
    height: 1rem;
    background: url(../../static/imgs/upload.png) no-repeat center center;
    background-size: cover;
}

.bookstype{
    width: 100%;
    height: 2.6rem;
    display: flex;
    line-height: 2.6rem;
    justify-content: space-between;
    padding: 0 .5rem;
    box-sizing: border-box;
    background: #fff;
    border-bottom: 1px solid #eee;
}
.bookstype>.title,label{
    font-size:0.85rem;
    font-weight:400;
    color:rgba(51,51,51,1);
}
.bookmiddle{
    width:100%;
    background:rgba(249,249,249,1);
}
.bookmiddle>li{
    width: 100%;
    display:flex;
    justify-content: space-around;
    padding: .5rem 1rem;
    box-sizing: border-box;
    position: relative;
}
.booklist{
    width: 100%;
    margin-top: 1rem;
    background: #fff;
}
.bookmiddle{
    background: #fff;
}
.bookmiddle>li{
    border-bottom: 1px solid #eee;
}
.right_del{
    display: inline-block;
    width: 1rem;
    height: 1rem;
    background: url(../../static/imgs/del.png) no-repeat center center;
    background-size: contain;
    position: absolute;
    right: .5rem;
}
.middle_right>h6{
    position: relative;
}
.middle_right>.last{
    height:1.4rem;
    font-size:1rem;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(255,59,48,1);
}
.last>span{
    float: right;
    display: flex;
    justify-content: space-between;
}
.last>span>i{
    display: inline-block;
    width: 1rem;
    height: 1rem;
    background: red;
    margin-top: .2rem;
}
.last>span>i.add{
    background: url(../../static/imgs/add.png) no-repeat center center;
    background-size: cover;
}
.last>span>i.reduce{
    background: url(../../static/imgs/reduce.png) no-repeat center center;
    background-size: cover;
}
.last>span>.text{
    display: inline-block;
    width: 1.5rem;
    height:1rem;
    font-size:0.7rem;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    text-align: center;
    line-height: 1rem;
    margin-top: .2rem;
}
.totalnum{
    text-align: right;
    padding: .5rem;
    box-sizing: border-box;
    font-size: .7rem;
}
.totalnum>span{
    font-weight: 550;
    color: red;
}
</style>



