<template>
    <div class="content">
        <div class="booktop">
            <div class="mainperson">
                <span></span>
                <input type="text" id="cardsNum2" v-model="addPeriodical.CREATORNAME"  @keyup="getGroup">
                <ul id="getGroup">
                    <li v-for="(v,i) in CREATORNAMEs" :key="i" @click="setvalue(v.NAME,v.ID)">{{v.NAME}}({{v.ID}})</li>
                </ul>
            </div>
            <div class="bookstype">
                <div class="title"></div>
                <div>
                    <input type="radio" id="churchyard" value="1" @change="isshowOA1(1)" v-model="addPeriodical.ISChINESE" name="radios">
                    <label for="churchyard"></label>
                    <input type="radio" id="overseas" value="0" @change="isshowOA1(0)" v-model="addPeriodical.ISChINESE" name="radios">
                    <label for="overseas"></label>
                </div>
            </div>
            <mt-field label="" placeholder="" v-model="addPeriodical.KINDNUM" @change="validnum(addPeriodical.KINDNUM)"></mt-field>
            <div class="bookstype">
                <div class="title"></div>
                <div>
                    <input type="radio" id="scientific" name="expenditure" value="" v-model="addPeriodical.FUNDTYPE">
                    <label for="scientific"></label>
                    <input type="radio" id="others" name="expenditure" value="" v-model="addPeriodical.FUNDTYPE">
                    <label for="others"></label>
                </div>
            </div>
            <mt-field label="" placeholder="" v-model="addPeriodical.FUNDNUMBER"></mt-field>
            <mt-field label="" placeholder="" v-model="addPeriodical.INVOICENUMBER" @change.native="checkInvoiceNum(addPeriodical.INVOICENUMBER)"></mt-field>
            <mt-field label="" placeholder="" v-model="addPeriodical.ALLMONEY" @change.native="isshowOA(addPeriodical.ALLMONEY)"></mt-field>
            <mt-field label="" placeholder="" v-model="addPeriodical.REASON"></mt-field>
            
        </div>
        <div class="bookbottom">
            <mt-cell title="" is-link  @click.native="upload">
                <img v-for="(v,i) in bookorders.slice(0,3)" :key="i" :src="domain+'/fileManage/get?ID='+v.ID" height="30px" width="30px">
                <span v-show="bookorders.length>3" style="color: #ccc">...</span>
            </mt-cell>
            <mt-cell title="" >
                <img v-show="FPfiles[0].ID" :preview='1' :src="domain+'/fileManage/get?ID='+FPfiles[0].ID" height="30px" width="30px">
                <span class="rightadd" @click="uploadFP">
                    <upload style="display:none;" ref="upload" :multiple='true' exts="png|jpg|bmp|gif|PNG|JPG|HEIC|JPEG|jpeg" v-on:choose="choosefiel" v-on:done="finish" :url="uploadUrl"></upload>
                </span>
            </mt-cell>
            <mt-cell title="OA" v-show="showOA == 'true'">
                <img v-show="OAfiles[0].ID" :preview='2' :src="domain+'/fileManage/get?ID='+OAfiles[0].ID" height="30px" width="30px">
                <span class="rightadd"  @click="uploadOA">
                    <upload style="display:none;" ref="uploadOA" :multiple='true' exts="png|jpg|bmp|gif|PNG|JPG|HEIC|JPEG|jpeg" v-on:choose="choosefiel" v-on:done="finishOA" :url="uploadUrl"></upload>
                </span>
            </mt-cell>
             <mt-cell title="" v-show="showOA == 'true'" >
                <img v-show="DOCfiles[0].ID" :preview='3' :src="domain+'/fileManage/get?ID='+DOCfiles[0].ID" height="30px" width="30px">
                <span class="rightadd" @click="uploadDOC">
                    <upload style="display:none;" ref="uploadDOC" :multiple='true' exts="png|jpg|bmp|gif|pdf|PNG|JPG|HEIC|JPEG|jpeg" v-on:choose="choosefiel" v-on:done="finishDOC" :url="uploadUrl"></upload>
                </span>
            </mt-cell>
        </div>
        <div class="bookfixedbottom1">
            <div class="bookfixedbottom1left" @click="Periodicalsave"></div>
            <div class="bookfixedbottom1right" @click="back"></div>
        </div>
        <div class="meng" v-show="ismeng">
            <p></p>
        </div>
    </div>
</template>
<script>
import code from '../util/code'
import upload from '../components/upload'
import Vue from 'vue'
import { global } from '../util/global'

export default {
    components: {
      upload  
    },
    data() {
        return {
            addPeriodical:{
                CREATORNAME:'',//
                INVOICENUMBER:'',//
                FUNDNUMBER: '',//
                FUNDTYPE: '',//
                KINDNUM: '',//
                ALLMONEY: '',//
                CREATORID: '',//ID
                REASON:'',//
                ISChINESE:'',//
            },
            CREATORNAMEs:[],//
            uploadUrl:'',//url
            bookorders:[],//
            FPfiles:[{ID:''}],//
            showOA :'',//OA
            OAfiles:[{ID:''}],//OA
            DOCfiles:[{ID:''}],//DOC
            isadd:'',//
            domain:global,
            ismeng:false,

        }
    },
    methods: {
        back(){
            this.$router.push('/');
            this.$store.commit('setselected','')

            // window.sessionStorage.setItem('selected','');
        },
        validnum(num){
            var re = /^\d+(\.\d+)?$/; // // /^[1-9]+[0-9]*]*$/ 
        if (!re.test(num)) {
            this.Toast("");
                this.addPeriodical.KINDNUM = '';
         return false;
        }
        },
        Periodicalsave(){
              if(!this.addPeriodical.CREATORNAME){
                this.Toast('');
            }else if(!this.addPeriodical.ISChINESE){
                this.Toast('');
            }else if(!this.addPeriodical.KINDNUM){
                this.Toast('');
            }else if(!this.addPeriodical.FUNDTYPE){
                this.Toast('');
            }else if(!this.addPeriodical.FUNDNUMBER){
                this.Toast('');
            }else if(!this.addPeriodical.INVOICENUMBER){
                this.Toast('');
            }else if(!this.addPeriodical.ALLMONEY){
                this.Toast('');
            }else if(!this.addPeriodical.REASON){
                this.Toast('');
            }else if(this.bookorders.length<1){
                this.Toast('');
            }else if(!this.FPfiles[0].ID){
                this.Toast('');
            }else{
                // this.$store.commit('setFundstype',this.bookstype);//
                var obj = {
                    ISChINESE:this.addPeriodical.ISChINESE,//
                    INVOICENUMBER:this.addPeriodical.INVOICENUMBER,//=
                    ALLMONEY:this.addPeriodical.ALLMONEY,//=
                    CREATORNAME:this.addPeriodical.CREATORNAME,//=
                    FUNDTYPE:this.addPeriodical.FUNDTYPE,//=
                    FUNDNUMBER:this.addPeriodical.FUNDNUMBER,//=
                    REASON:this.addPeriodical.REASON,//
                    KINDNUM:this.addPeriodical.KINDNUM,//=
                    CREATORID:window.sessionStorage.getItem('CREATORNAMEID')//=
                }
                // window.sessionStorage.setItem('addbook',JSON.stringify(obj));\
                var periodical = {//--
                    ID: "",  //()
                    "FUNDTYPE": obj.FUNDTYPE,  //(  /=)
                    "INVOICENUMBER":  obj.INVOICENUMBER, //=
                    "FUNDNUMBER":  obj.FUNDNUMBER, //=
                    "KINDNUM":  obj.KINDNUM, //=
                    "ALLMONEY":  obj.ALLMONEY,//=
                    "ISCHINESE":  obj.ISChINESE, //1  0
                    "CREATORNAME":  obj.CREATORNAME, //1.3= 
                    "CREATORID":  obj.CREATORID, //ID1.3=
                    "REASON":  obj.REASON, //
                }
                // console.log(this.isadd)
                    if(this.isadd == 'false'){
                        periodical.ID = window.sessionStorage.getItem('editPeriodicalID');
                    }else{
                        periodical.ID = '';
                    }
                    //
                    var files = [];
                    this.bookorders.forEach(e=>{
                        files.push({
                            TYPE:0,
                            URL:e.ID
                        })
                    })
                    //
                    files.push({
                         TYPE:2,
                         URL:this.FPfiles[0].ID
                    })
                    if(window.sessionStorage.getItem('showOA')== 'true'){
                        //OA
                        files.push({
                            TYPE:3,
                            URL:this.OAfiles[0].ID
                        })
                        //
                        files.push({
                            TYPE:4,
                            URL:this.DOCfiles[0].ID
                        })
                    }
                    this.ismeng = true;
                    // return false
                    //
                    this.util.postAjax({
                        code:code.base,
                        url:code.Periodicalsave,
                        data:{
                            data: JSON.stringify({
                                'periodical':periodical,
                                'files':files,
                            })
                        }
                    }).then( res => {
                        if(res.success){
                            this.ismeng = false;
                            this.Toast(res.message);
                            this.$router.push('/');
                            this.$store.commit('setselected','')
                            //
                            // window.sessionStorage.removeItem('mainpersonID')
                            window.sessionStorage.removeItem('addlist')
                            window.sessionStorage.removeItem('FPfiles')
                            window.sessionStorage.removeItem('addbook')
                        }else{
                            this.ismeng = false;
                            // this.Toast(res.message);
                        }
                    })

            }
            
        },
          //url
        uploadFile(){
          this.util.getUrlByCode(code.base,code.fileManageupload).then(res => {
            this.uploadUrl = res;
            
          })
        },
        choosefiel(file){
            console.log("");
        },
        finish(res){
            console.log("");
            this.FPfiles = []; 
            res.FILE.forEach(element => {
                this.FPfiles.push(element);
            });
            Vue.set(this.FPfiles,res);
            window.sessionStorage.setItem('FPfiles',JSON.stringify(this.FPfiles))
        },
        finishOA(res){
            this.OAfiles = [];
            res.FILE.forEach(element => {
                this.OAfiles.push(element);
            });
            Vue.set(this.OAfiles,res);
            window.sessionStorage.setItem('OAfiles',JSON.stringify(this.OAfiles))
        },
        finishDOC(res){
            this.DOCfiles = [];
            res.FILE.forEach(element => {
                this.DOCfiles.push(element);
            });
            Vue.set(this.DOCfiles,res);
            window.sessionStorage.setItem('DOCfiles',JSON.stringify(this.DOCfiles))
        },
        //
        getGroup(){
            // if(!this.addPeriodical.CREATORNAME){
            //     return false;
            // }
            this.util.postAjax({
                code:code.base,
                url:code.bookgetGroup,
                data:{
                    page:1,
                    limit:10,
                    filter: JSON.stringify({
                        'KEYWORD':this.addPeriodical.CREATORNAME,
                    })
                }
            }).then( res => {
                if(res.items.length == 0){
                    this.CREATORNAMEs = [];
                    this.addPeriodical.CREATORNAME = '';
                }else{
                    this.CREATORNAMEs = res.items;
                }
                
            })
        },
        //li
        setvalue(data,id){
            this.addPeriodical.CREATORNAME = data;
            //id
            window.sessionStorage.setItem('CREATORNAMEID',id)
            this.CREATORNAMEs = [];
        },
        //
        upload(){
            //
            window.sessionStorage.setItem('formbook','noformbook');
            var obj = {
                ISChINESE:this.addPeriodical.ISChINESE,//
                INVOICENUMBER:this.addPeriodical.INVOICENUMBER,//
                ALLMONEY:this.addPeriodical.ALLMONEY,//
                CREATORNAME:this.addPeriodical.CREATORNAME,//
                FUNDNUMBER:this.addPeriodical.FUNDNUMBER,//
                INVOICENUMBER:this.addPeriodical.INVOICENUMBER,//
                FUNDTYPE:this.addPeriodical.FUNDTYPE,//
                KINDNUM:this.addPeriodical.KINDNUM,//
                REASON:this.addPeriodical.REASON,//
            }
            window.sessionStorage.setItem('addPeriodical',JSON.stringify(obj))
            this.$router.push('addupload')
        },
        //OA
        uploadOA(){
            this.$refs.uploadOA.toupload();
        },
        //pdf
        uploadDOC(){
            this.$refs.uploadDOC.toupload();
        },
        //
        uploadFP(){
            this.$refs.upload.toupload();
        },
        //
        checkInvoiceNum(data){
            var ID= '';
            if(window.sessionStorage.getItem('isperiodicaladd')=='true'){
                ID = ''
            }else{
                ID = window.sessionStorage.getItem('editPeriodicalID');
            }
            if(data){
                this.util.postAjax({
                    code:code.base,
                    url:code.PeriodicalcheckInvoiceNum,
                    data:{
                        INVOICENUMBER:data,
                        ID:ID
                    }
                }).then( res => {
                    if(!res.status){
                        this.Toast("");
                        this.addPeriodical.INVOICENUMBER = ''
                    }
                })
            }else{
                this.Toast('')
            }
        },
        //OA
        isshowOA(num){
            if(this.addPeriodical.ISChINESE == 1){//
                if(num > 2000){
                    this.showOA = 'true';
                    window.sessionStorage.setItem('showOA','true');
                }else{
                    this.showOA = 'false';
                    window.sessionStorage.setItem('showOA','false');
                }
            }else{
                if(num > 5000){
                    this.showOA = 'true';
                    window.sessionStorage.setItem('showOA','true');
                }else{
                    this.showOA = 'false';
                    window.sessionStorage.setItem('showOA','false');
                }
            }
        },
        isshowOA1(num){
            if(num == 1){//
                if(this.addPeriodical.ALLMONEY > 2000){
                    this.showOA = 'true';
                    window.sessionStorage.setItem('showOA','true');
                }else{
                    this.showOA = 'false';
                    window.sessionStorage.setItem('showOA','false');
                }
            }else{
                if(this.addPeriodical.ALLMONEY > 5000){
                    this.showOA = 'true';
                    window.sessionStorage.setItem('showOA','true');
                }else{
                    this.showOA = 'false';
                    window.sessionStorage.setItem('showOA','false');
                }
            }
        },


    },
    created() {
        this.uploadFile();
        this.isadd = window.sessionStorage.getItem('isperiodicaladd')
        //
        if(window.sessionStorage.getItem('addPeriodical')){
            this.addPeriodical = JSON.parse(window.sessionStorage.getItem('addPeriodical'));
        }
        //
        this.bookorders = this.$store.state.selectimgs;
        //
        if(window.sessionStorage.getItem('FPfiles')){
            this.FPfiles = JSON.parse(window.sessionStorage.getItem('FPfiles'))
        }
        if(window.sessionStorage.getItem('OAfiles')){
            this.OAfiles = JSON.parse(window.sessionStorage.getItem('OAfiles'));
            this.DOCfiles = JSON.parse(window.sessionStorage.getItem('DOCfiles'))
        }
        if(this.addPeriodical.ISChINESE == 1){
            if(this.addPeriodical.ALLMONEY>2000){
                this.showOA = 'true';
            }else{
                this.showOA = 'false';
            }
        }else{
            if(this.addPeriodical.ALLMONEY>5000){
                this.showOA = 'true';
            }else{
                this.showOA = 'false';
            }
        }
    },
}
</script>
<style scoped>
.content{
    background:#F5F5F5; 
    /* overflow: scroll; */
    width: 100%;
    padding-bottom: 2.25rem;
    /* height: calc(100% - 3rem);
    position: fixed; */
}
.booktop{
    width: 100%;
}
.bookbottom{
    margin-top: 1rem;
}
.rightadd{
    display: inline-block;
    width: 1rem;
    height: 1rem;
    background: url(../../static/imgs/upload.png) no-repeat center center;
    background-size: cover;
}
.bookfixedbottom1{
    width:100%;
    height:2.25rem;
    display: flex;
    justify-content: space-around;
    position: fixed;
    left: 0;
    bottom: 0;
    /* background:rgba(255,255,255,1);
    border:0.03rem solid rgba(204,204,204,1); */
}
.bookfixedbottom1>div{
    width: 50%;
    height: 100%;
    font-size:0.65rem;
    color:rgba(255,255,255,1);
    line-height: 2.25rem;
}
.bookfixedbottom1left{
    background:rgba(71,122,219,1);
}
.bookfixedbottom1>div.bookfixedbottom1right{
    background: #fff;
    border:0.03rem solid rgba(204,204,204,1);
    color:rgba(51,51,51,1);
}
.bookstype{
    width: 100%;
    height: 2.6rem;
    display: flex;
    line-height: 2.6rem;
    justify-content: space-between;
    padding: 0 .5rem;
    box-sizing: border-box;
    background: #fff;
    border-bottom: 1px solid #eee;
}
.bookstype>.title,label{
    font-size:0.85rem;
    font-weight:400;
    color:rgba(51,51,51,1);
}
.booktop{
    width: 100%;
}

</style>



