import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        selectimgs:'',//
        Fundstype:'',///
        selected:'',//
    },
    mutations:{
        setselectimgs(state,val){
            state.selectimgs = val;
        },
        setFundstype(state,val){
            state.Fundstype = val;
        },
        setselected(state,val){
            state.selected = val;
        },
    }
})

export default store;


